.form_covid {
  margin: 5rem auto;
}

.form_covid__title {
  text-align: center;
  margin-bottom: 1rem;
  color: #06D6A0;
}

.form_covid__left {
  display: none;
}

.form {
  width: 100%;
}

.form__group {
  margin-bottom: 1rem;
}

.form__label {
  font-size: 1rem;
  display: block;
  margin-bottom: 0.5rem;
}

.form__control {
  font-size: 1rem;
  color: #073B4C;
  display: block;
  width: 100%;
  padding: 1rem 0.5rem;
  border: 1px solid #64748B;
  border-radius: 10px;
  background-color: #fff;
  outline: none;
}

.form__control:focus {
  border: 1px solid #06D6A0;
}

.form__button {
  padding: 0.8rem 1rem;
  display: block;
  width: 100%;
  border-radius: 10px;
  border: none;
  font-size: 1rem;
  font-weight: 600;
  background-color: #06D6A0;
  color: #fff;
  cursor: pointer;
}

@media (min-width: 768px) {
  .form {
    width: 500px;
    margin: 0 auto;
  }
  
}

@media (min-width: 992px) {
  .form_covid__left {
    flex-basis: 60%;
    display: block;
  }

  .form_covid__image {
    width: 500px;
  }

  .form_covid__right {
    width: 100%;
    flex-basis: 40%;
  }

  .form_covid {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}