.navbar {
  background-color: #06D6A0;
  color: #fff;
  padding: 1rem;
}

.navbar__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navbar__toggle {
  font-size: 2rem;
  cursor: pointer;
}

@media(min-width: 768px) {

}

@media(min-width: 992px) {
  .navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .navbar__toggle {
    display: none;
  }
}