.card {
  margin: 1rem 0;
  padding: 5rem 0;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
  border-radius: 10px;
}

.card__status {
  margin-bottom: 1rem;
}

.card__total {
  font-size: 3rem;
  margin-bottom: 1rem;
}

.card__detail {
  color: #64748B;
  font-size: 1.2rem;
  font-weight: 500;
}

.positif {
  color: #06D6A0;
}

.sembuh {
  color: #118AB2;
}

.meninggal {
  color: #EF476F;
}

@media (min-width: 768px) {
  .card {
    width: 350px;
    margin-left: auto;
    margin-right: auto;
  }
}

@media (min-width: 992px) {
  .card {
    width: auto;
    flex-basis: 30%;
  }
}