.hero {
  margin: 3rem auto;
}

.hero__left {
  text-align: center;
}

.hero__title {
  color: #06D6A0;
}

.hero__subtitle {
  color: #118AB2;
}

.hero__description {
  margin: 1rem auto 1.3rem;
  color: #64748B;
  max-width: 500px;
}

.hero__button {
  padding: 0.8rem 3rem;
  border: none;
  border-radius: 10px;
  background-color: #06D6A0;
  color: #fff;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;

}

.hero__right {
  margin-top: 3rem;
  width: 100%;
  text-align: center;
}

.hero__image {
  max-width: 100%;
  border-radius: 1rem;
}

@media (min-width: 768px) {
  .hero__image {
    width: 500px;
  } 
}

@media (min-width: 992px) {
  .hero {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .hero__left {
    text-align: left;
    flex-basis: 40%;
  }

  .hero__right {
    flex-basis: 60%;
    text-align: right;
  }
}